@import "https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap";
body {
  font-family: Lato, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.header nav {
  text-transform: uppercase;
  font-size: 12px;
}

.logo img {
  max-height: 50px;
}

.navigation-wrapper {
  align-items: center;
  display: flex;
}

.navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation ul li {
  margin-right: 20px;
}

.navigation ul li:last-child {
  margin-right: 0;
}

.navigation ul li a {
  color: #333;
  padding: 10px;
  font-weight: bold;
  text-decoration: none;
}

.navigation ul li a:hover {
  text-decoration: underline;
}

.contact-info {
  color: #333;
  flex-direction: column;
  align-items: flex-end;
  margin-left: 20px;
  display: flex;
}

.page-header__contacts-item {
  color: #333;
  align-items: center;
  padding: 0 0 5px;
  text-decoration: none;
  display: flex;
}

.page-header__contacts-item:hover {
  color: #00f;
}

.page-header__contacts-item svg {
  fill: currentColor;
  width: 16px;
  height: 12px;
  margin-right: 5px;
}

.footer {
  text-align: center;
  background-color: #fff;
  padding: 20px;
}

.footer img {
  max-width: 150px;
}

